.tasks {
    max-width: 40rem;
    width: 90%;
    margin: 2rem auto;
    animation: tasks-appear 1s ease-out forwards;
}

.tasks ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

@keyframes tasks-appear {
    from {
        opacity: 0;
        transform: translateY(3rem);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}